@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
.modal-background {
  position: fixed;
  z-index: 50;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  display: none;
  visibility: hidden;
}

.modal {
  position: relative;
  z-index: 100;
  color: #575757;
  background-color: #FFF;
  display: flex;
  min-height: 30rem;
  width: 65%;
  margin: 0 auto;
  top: 20%;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#map {
  border: 4px solid #575757;
  margin-bottom: 1rem;
}
.panel {
  background-color: #e2e2e2;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 10rem;
  padding: 1.0rem;
  box-shadow: 2px 0px 25px 0px rgba(0,0,0,0.25);
}
#next-turn-button {
  position: relative;
  top: 75%;
}
body, html {
  font-family: 'Press Start 2P', cursive;
  margin: 0;
  padding: 0;
  line-height: 1.2rem;
  height: 100%;
  width: 100%;
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  margin-top: 3rem;
}
